import Vue from 'vue'
import App from './App.vue'
// 
import "./assets/css/reset.css"
// 
import VueRouter from "vue-router";
// element.ui
import ElementUI from 'element-ui';
import store from '@/vuex/store' // store
import 'element-ui/lib/theme-chalk/index.css';
import less from 'less'

Vue.use(VueRouter);
Vue.use(less)
Vue.use(ElementUI);

// 
import index from './views/index.vue'
import home from './views/home.vue'
import main from './views/main.vue'
import news from './views/news.vue'
import new1 from './views/new-1.vue'
import new2 from './views/new-2.vue'
import new3 from './views/new-3.vue'
import new4 from './views/new-4.vue'
import new5 from './views/new-5.vue'
import new6 from './views/new-6.vue'
import new7 from './views/new-7.vue'
import new8 from './views/new-8.vue'
import new9 from './views/new-9.vue'
import newA from './views/new-A.vue'
import newB from './views/new-B.vue'
import newC from './views/new-C.vue'
import shop from './views/shop.vue'
import rank from './views/ranking.vue'

const router = new VueRouter({
	routes: [{
		// 
			path: '/',
			redirect: '/index',
		},
		{
			// 
			path: '/index',
			component: index,
		},
		{
			// 
			path: '/home',
			component: home,
		},
		{
			// 
			path: '/shop',
			component: shop,
		},
		{
			// 
			path: '/rank',
			component: rank,
		},
		{
			// 
			path: '/main',
			component: main,
		},
		{
			// 
			path: '/news',
			component: news,
		},
		{
			// 
			path: '/news/new1',
			component: new1,
		},
		{
			// 
			path: '/news/new2',
			component: new2,
		},
		{
			// 
			path: '/news/new3',
			component: new3,
		},
		{
			// 
			path: '/news/new4',
			component: new4,
		},
		{
			// 
			path: '/news/new5',
			component: new5,
		},
		{
			// 
			path: '/news/new6',
			component: new6,
		},
		{
			// 
			path: '/news/new7',
			component: new7,
		},
		{
			// 
			path: '/news/new8',
			component: new8,
		},
		{
			// 
			path: '/news/new9',
			component: new9,
		},
		{
			// 
			path: '/news/newA',
			component: newA,
		},
		{
			// 
			path: '/news/newB',
			component: newB,
		},
		{
			// 
			path: '/news/newC',
			component: newC,
		},
	]
})

Vue.config.productionTip = false

// router-link
router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
})

new Vue({
	render: h => h(App),
	router,
	store
}).$mount('#app')
